import { observer } from 'mobx-react-lite';
import React from 'react';
import { useStore } from '../../../../store/AuthProvider';
import CardProduct from '../../../SharedLayout/CardProduct/CardProduct';
import { StyledFilterResults } from './FilterResult.styled';
import useWindowSize from '../../../../hooks/useWindowSize';
import MobileCardProduct from '../../../SharedLayout/MobileCardProduct/MobileCardProduct';

const FilterResult = observer(() => {
  const { screen } = useWindowSize();
  const store = useStore();
  const {
    catalog: { filteredProducts, state },
  } = store;
  return (
    <StyledFilterResults>
      {state === 'done'
        ? filteredProducts.map(
            ({
              id,
              title,
              description,
              short_description,
              image_url,
              slug,
              price,
              promotional_price,
              is_promotional,
              is_new,
              quantity,
              country,
              brand,
              category,
              animal,
            }) => {
              return screen === 'mobile' ? (
                <MobileCardProduct
                  key={id}
                  id={id}
                  title={title}
                  short_description={short_description}
                  description={description}
                  image_url={image_url}
                  slug={slug}
                  price={price}
                  promotional_price={promotional_price}
                  is_promotional={is_promotional}
                  is_new={is_new}
                  quantity={quantity}
                  country={country}
                  brand={brand}
                  category={category}
                  animal={animal}
                />
              ) : (
                <CardProduct
                  key={id}
                  id={id}
                  title={title}
                  description={description}
                  short_description={short_description}
                  image_url={image_url}
                  slug={slug}
                  price={price}
                  promotional_price={promotional_price}
                  is_promotional={is_promotional}
                  is_new={is_new}
                  quantity={quantity}
                  country={country}
                  brand={brand}
                  category={category}
                  animal={animal}
                />
              );
            }
          )
        : null}
    </StyledFilterResults>
  );
});

export default FilterResult;
